<template>
  <!-- 登录页面 -->
  <div class="login-container">
    <div class="login">
      <iForm :model="loginForm" :rules="loginRule" ref="loginForm">
        <FormItem prop="user">
          <iInput v-model="loginForm.user" type="text" placeholder="请输入用户名或邮箱" />
        </FormItem>
        <FormItem prop="password">
          <iInput v-model="loginForm.password" type="password" placeholder="请输入密码" />
        </FormItem>
        <FormItem class="less-margin">
          <iButton @click="handleLogin('loginForm')" type="primary" long>登录</iButton>
        </FormItem> 
      </iForm>
    </div>
    <div class="links">
      <a>忘记密码</a><a href="/register">立即注册</a>
    </div>
  </div>  
</template>

<script>
  import iForm from 'iview/src/components/form'
  import iInput from 'iview/src/components/input'
  import iButton from 'iview/src/components/button'
  import Message from 'iview/src/components/message'

  // 验证密码
  const validatePwd = (rule, value, callback) => {
    if (value.trim() === '') {
      callback(new Error('密码不能为空'))
    } else {
      callback()
    }
    // 异步验证
  }

  export default {
    name: 'login',
    components: {
      iForm,
      FormItem: iForm.Item,
      iInput,
      iButton
    },
    data () {
      return {
        loginForm: {
          user: '',
          password: ''
        },
        loginRule: {
          user: [
            { required: true, message: '请填写用户名', trigger: 'blur' }
          ],
          password: [
            { validator: validatePwd, trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleLogin (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post('/pub/index/login', this.loginForm).then((response) => {
              // 响应成功回调
              console.log('response:::' + response.data)
              const res = response.data
              if (res.flag) {
                this.$Message.success('登录成功，即将跳转至首页!')
                this.$router.push({path: '/console', query: {name: res.obj.user_name}})
              } else {
                this.$Message.error('登录失败，请重新登录!')
              }
            }, (response) => {
              // 响应错误回调
              this.$Message.error('登录失败，请重新登录!')
            })
          } else {
            this.$Message.error('表单验证失败!')
          }
        })
      }
    },
    mounted () {
      this.$Message = Message
    }
  }
</script>
<style lang="scss">
  $loginBoxWidth: 280px;
  $logoHeight: 120px;   /* logo: 228*72px */
  $defaultFormItemMargin: 15px;
  .login-container {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -$loginBoxWidth/2;
    margin-top: -200px;
    width: $loginBoxWidth;
    padding-top: $logoHeight;
    background: url('../assets/logo.png') center 0 no-repeat;
  }
  .less-margin {
    margin-bottom: $defaultFormItemMargin/2;
  }
  .links {
    display: flex;
    justify-content: space-between;
  }
  .ivu-form-item {
    margin-bottom: $defaultFormItemMargin;
  }
  .ivu-form-item-content .ivu-form-item-error-tip {
    width: 100px;
    top: 20%;
    left: 104%;
  }
</style>



