<template>
  <!-- 注册页面 -->
  <div class="register-container">
    <div class="register">
      <iForm :model="loginForm" :rules="loginRule" ref="loginForm">
        <FormItem prop="user">
          <iInput v-model="loginForm.user" type="text" placeholder="请输入用户名或邮箱" />
        </FormItem>
        <FormItem prop="password">
          <iInput v-model="loginForm.password" type="password" placeholder="请输入密码" />
        </FormItem>
        <FormItem prop="passwordCheck">
          <iInput v-model="loginForm.passwordCheck" type="password" placeholder="请再次输入密码" />
        </FormItem>
        <FormItem prop="validatorCode">
          <iInput style="width: 40%;" v-model="loginForm.validatorCode" type="password" placeholder="验证码" />
          <img />
        </FormItem>
        <FormItem prop="agreementCheck">
          <iCheckbox v-model="loginForm.agreementCheck">我已经阅读并同意<a>隐私政策</a>和<a>服务条款</a></iCheckbox>
        </FormItem>
        <FormItem class="less-margin">
          <iButton @click="handleLogin('loginForm')" type="primary" long>注册</iButton>
        </FormItem> 
      </iForm>
    </div>
    <div class="links">
      <a href="/login">已有账号，请登录>></a>
    </div>
  </div>  
</template>

<script>
  import iForm from 'iview/src/components/form'
  import iInput from 'iview/src/components/input'
  import iButton from 'iview/src/components/button'
  import iCheckbox from 'iview/src/components/checkbox'
  import Message from 'iview/src/components/message'

  export default {
    name: 'login',
    components: {
      iForm,
      FormItem: iForm.Item,
      iInput,
      iButton,
      iCheckbox
    },
    data () {
      // 验证密码
      const validatePwd = (rule, value, callback) => {
        if (value.trim() === '') {
          callback(new Error('密码不能为空'))
        } else {
          callback()
        }
        // 异步验证
      }
      // 确认密码
      const validatePwdCheck = (rule, value, callback) => {
        if (value.trim() === '') {
          callback(new Error('确认密码不能为空'))
        } else if (value !== this.loginForm.password) {
          callback(new Error('两次密码输入不一致！'))
        } else {
          callback()
        }
      }
      return {
        loginForm: {
          user: '',
          password: '',
          passwordCheck: '',
          validatorCode: ''
        },
        loginRule: {
          user: [
            { required: true, message: '请填写用户名', trigger: 'blur' }
          ],
          password: [
            { validator: validatePwd, trigger: 'blur' }
          ],
          passwordCheck: [
            { validator: validatePwdCheck, trigger: 'blur' }
          ],
          validatorCode: [
            {required: true, message: '请输入验证码'}
          ],
          agreementCheck: [
            {required: true, message: '请勾选阅读同意'}
          ]
        }
      }
    },
    methods: {
      handleLogin (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            console.log(this.loginForm.user)
            this.$http.post('/pub/index/register', this.loginForm).then((response) => {
              // 响应成功回调
              const res = response.data
              if (res.flag) {
                console.log('user_name:::' + res.user_name)
                this.$router.push({path: '/console', query: {name: res.obj.user_name}})
                this.$Message.success('注册成功，即将跳转至首页或者控制台!')
              } else {
                this.$Message.error('注册失败，请重新注册!')
              }
            }, (response) => {
              // 响应错误回调
              this.$Message.error('注册失败，请重新注册!')
            })
          } else {
            this.$Message.error('表单验证失败!')
          }
        })
      }
    },
    mounted () {
      this.$Message = Message
    }
  }
</script>
<style lang="scss">
  $loginBoxWidth: 280px;
  $logoHeight: 120px;   /* logo: 228*72px */
  $defaultFormItemMargin: 15px;

  .register-container {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -$loginBoxWidth/2;
    margin-top: -200px;
    width: $loginBoxWidth;
    padding-top: $logoHeight;
    background: url('../assets/logo.png') center 0 no-repeat;
  }
  .less-margin {
    margin-bottom: $defaultFormItemMargin/2;
  }
  .ivu-form-item {
    margin-bottom: $defaultFormItemMargin;
  }
  .ivu-form-item-content .ivu-form-item-error-tip {
    width: 100px;
    top: 20%;
    left: 104%;
  }
</style>



